.task {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    border-bottom: 1px solid #ccc;
    padding-bottom: 5px;
}

.task input[type='checkbox'] {
    -ms-flex: 1 1;
        flex: 1 1;
    color: pink;
}

.task p {
    -ms-flex: 10 1;
        flex: 10 1;
    font-size: 18px;
    color: rgb(139, 138, 138);
    font-weight: bold;
}

p.done {
    text-decoration: line-through;
    color: rgb(218, 55, 55);
    font-style: italic;
}

.task button {
    -ms-flex: 1 1;
        flex: 1 1;
    border: none;
    background-color: rgb(218, 55, 55);
    color: rgb(245, 241, 241);
    width: 80px;
    padding: 11px;
    border-radius: 3px;
    font-size: 12px;
    outline: none;
    font-weight: bold;
}

.task button:hover {
    background-color: rgb(208,0,0);
    color: #fff;
}


